import { DataVal } from "@atlasacademy/api-connector";
export default function (vals) {
    return Object.values(DataVal.DataValField).filter(function (field) {
        var values = vals.map(function (val) { return val[field]; });
        return new Set(values.map(function (value) {
            if (Array.isArray(value))
                return value.join(',');
            return value;
        })).size > 1;
    });
}
